export default {
  components: {},
  props: {
    symbleParameArray: '',
    dailyDetail: {
      type: Object,
      default: function () {
        return {}
      }
    },
    dcs: {
      type: Object,
      default: function () {
        return {}
      }
    },
    symblefrom: '',
    symbleto: ''
  },
  data () {
    return {
      count: [],//24小时累计
      countData: [],//24小时累计
      ws: null,//websocket对象
      timeInterval: null,//对象
      lockReconnect: false,//避免重复连接
      currentState: true,
      background: '',
    };
  },
  created () {
  },
  mounted () {
    this.$nextTick(() => {
      this.background = this.$store.state.background
      // this.init();
      // 登录成功
      this.$bus.on("Login-Success", () => {
        if (this.$store.state.token) {
          this.ws.send(JSON.stringify({ "sub": "OrderChange", "coinTeam": "XBIT-USDT", "token": this.$store.state.token }))
        }
      });
    })
  },
  //销毁前调用
  destroyed () {
    clearInterval(this.timeInterval)
    this.currentState = false
  },
  methods: {
    init () {
      this.initWebsocket()
    },
    async initWebsocket () {
      //获取交易对配置
      this.ws = new WebSocket("wss://www.buybit.com" + '/api/v1/buybit/power/ws/merge');
      this.ws.onopen = () => {
        this.ws.send(JSON.stringify({ "sub": "TopCount", "coinTeam": "XBIT-USDT" }))
        this.ws.send(JSON.stringify({ "sub": "Depth", "coinTeam": "XBIT-USDT" }))
        this.ws.send(JSON.stringify({ "sub": "Trade", "coinTeam": "XBIT-USDT" }))
        if (this.$store.state.token) {
          this.ws.send(JSON.stringify({ "sub": "OrderChange", "coinTeam": "XBIT-USDT", "token": this.$store.state.token }))
        }
      }
      this.ws.onmessage = (event) => {
        this.handleData(event.data);
      }
      this.ws.onclose = () => {
        console.log('close6');
        this.reconnect()
      }
      this.ws.onerror = err => {
        console.log('error', err);
        this.reconnect()
      }
    },
    //短线重连
    reconnect () {
      if (this.lockReconnect || !this.currentState) return;
      this.lockReconnect = true;
      //没连接上会一直重连，设置延迟避免请求过多
      setTimeout(() => {
        console.log('没连接上会一直重连，设置延迟避免请求过多')
        // this.init();
        this.$bus.emit('reconnect');
        this.lockReconnect = false;
      }, 5000); //这里设置重连间隔(ms)
    },
    // 处理接收到的信息
    handleData (msg) {
      let data = JSON.parse(msg);
      if (data.sub == 'TopCount') {
        // console.log('TopCount')
        this.onMessage(data);
      }
      if (data.sub == 'Depth') {
        // console.log('Depth')
        //将深度数据传递
        this.$bus.emit('DepthHashRateData', data)
      }
      if (data.sub == 'Trade') {
        // console.log('Trade')
        //将实时成交数据传递
        this.$bus.emit('TradeHashRateData', data)
      }
      if (data.sub == 'OrderChange') {
        // console.log('OrderChange')
        //监听订单
        this.$bus.emit('HashRateOrderChange')
      }
    },
    onMessage (data) {
      clearInterval(this.timeInterval)
      this.timeInterval = setInterval(() => {
        this.ws.send(JSON.stringify({
          "ping": new Date().getTime()
        }));
      }, 30000)
      if (data.data) {
        let res = data.data
        this.$set(this.countData, 'close', res.close);
        this.$set(this.countData, 'cnyClose', res.cnyClose);
        this.$set(this.countData, 'percent', res.percent);
        this.$set(this.countData, 'high', res.high);
        this.$set(this.countData, 'low', res.low);
        this.$set(this.countData, 'vol', res.vol);
        //将最新实时信息发送给深度、实时、限价
        this.$bus.emit('HashRateNewTitleData', res)
      }
    },

    //切换主题颜色
    changeTheme (state) {
      if (state) {
        this.$store.commit('SET_BACKGROUND', 'night')
      }
      else {
        this.$store.commit('SET_BACKGROUND', 'day')
      }
      this.$bus.emit("changeTheme");
    }
  },
}

